import React from 'react';
import Link from 'next/link';
import {
  MdSpaceDashboard, MdPeople, MdEvent, MdAttachMoney, MdGroups, MdVolunteerActivism,
  MdMessage, MdPermMedia, MdSettings, MdPerson
} from "react-icons/md";

// Add 'href' for routing to each item
const NAV_ITEMS = [
  { label: "Dashboard", icon: <MdSpaceDashboard />, href: "/AdminDash" },
  { label: "Profile", icon: <MdPerson />, href: "/Profile" },
  { label: "Members", icon: <MdPeople />, href: "/Members" },
  { label: "Attendance", icon: <MdEvent />, href: "/attendance" },
  { label: "Events", icon: <MdEvent />, href: "/events" },
  { label: "Donations", icon: <MdAttachMoney />, href: "/donations" },
  { label: "Groups", icon: <MdGroups />, href: "/groups" },
  { label: "Volunteers", icon: <MdVolunteerActivism />, href: "/volunteers" },
  { label: "Communication", icon: <MdMessage />, href: "/communication" },
  { label: "Media", icon: <MdPermMedia />, href: "/media" },
  { label: "Settings", icon: <MdSettings />, href: "/settings" },
];

const SideBarDash = () => {
  return (
    <div className='h-auto max-w-screen w-68 bg-white pt-10  rounded-lg m-1'>
      <div className='flex-col justify-center items-center w-64 flex'>
        <h1 className='text-2xl font-semibold text-persian-blue-700'>Main Menu</h1>
        <ul className="flex flex-col justify-center p-2 text-3xl ">
          {NAV_ITEMS.map(({ label, icon, href }) => (
            <Link href={href} >
            <li
              key={label}
              className="
                transition-all duration-100
                flex
                pl-5
                items-center
                text-xl
                gap-3
                p-5
                text-persian-blue-600
                group
                hover:bg-persian-blue-600
                hover:text-persian-blue-50
                hover:items-center
                hover:rounded-lg
              "
            >
              
                <span className="group-hover:scale-110 transition-all duration-100">{icon}</span>
                <span className="group-hover:scale-105 transition-all duration-100">{label}</span>
              
            </li>
            </Link>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default SideBarDash;
